* {
  font-family: SFMono-Regular,Menlo,Monaco,Consolas,"Liberation Mono","Courier New",monospace;
  margin: 0; 
  padding: 0;
}

body {
  background: #1d1d1d;
}

#buttons {
  display: flex;
  padding-right: 120px;
  justify-content: center;
}

button {
  padding: 8px;
  cursor: pointer;
  margin: 4px;
  border: 1px solid #3d3d3d;
  border-radius: 20px;
  background: #3d3d3d;
  outline: none;
  color:#9d9d9d;
}

button:hover {
  background: #4d4d4d;
}

.active {
  background:#4285f4 !important;
  color: #fff !important;
  border: 1px solid #4285f4 !important;
}

#container {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

#ui {
  display: flex;
  flex-direction: column;
  padding: 10px;
  margin-top: 15px;
  margin-left: 20px;
}

#inputRow {
  margin-bottom: 40px;
}

#inputRow input, #outputNum {
  width: 70px;
  height: 40px;
  font-size: 40px;
  text-align: left;
  outline: none;
  color: #4285f4;
  font-weight: bold;
}

input {
  outline: none;
  border: none;
  background: none;
  border-bottom: 1px solid #9d9d9d;
}

input:hover, input:focus {
  border-bottom: 1px solid #4285f4;
}

#value {
  display: flex;
  justify-content: center;
  padding-right: 120px;
  margin-bottom: 20px;
  align-items: center;
}

#value input {
  font-size: 20px;
  width: 50px;
  height: 25px; 
  margin: 0 5px;
  color: #4285f4;
}

#value span {
  position: relative;
}

#value span:after {
  content: ',';
  width: 5px;
  height: 5px;
  position: absolute;
  left: 55px;
  top: 5px;
  color: #7d7d7d;
}

#value span:last-child:after {
  display: none;
}

label {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 5px;
  color: #9d9d9d !important;
}

#outputNum {
  display: inline-block;
}

